<!-- 未中奖弹窗 -->
<template>
	<xh-dialog ref="xhDialog" animName="fadeInDownBig" >
		<view class="again">
			<!-- 错误提示 -->
			<view class="msg">{{msg}}</view>
			<!-- 重新再来 -->
			<view class="xh-toast-button again-bottom" @click="$emit('again')">重新核销</view>
		</view>
	</xh-dialog>
</template>

<script>
	export default {
		data() {
			return {
				msg: ''
			}
		},
		methods: {
			show(msg) {
				this.msg = msg;
				this.$refs.xhDialog.show();
			}
		}
	}
</script>

<style lang="scss">
	.again {
		position: relative;
		z-index: 1;
		padding: 0 RPX(38);

		.msg {
			padding-top: RPX(38);
			padding-bottom: RPX(80);
			font-size: RPX(25);
			color: #fff;
			text-align: center;
			font-weight: bold;
		}
		.again-bottom{
			margin-bottom: RPX(20);
		}
	}
</style>
